.FiveLinks {
  border: var(/*--orange*/ --headerColor) 2px solid;
  border-radius: 5px;
  box-shadow: 0px 10px 5px #171d20;
  margin-top: 0;
  margin-left: 1.5em;
  margin-right: 1.5em;
  padding: 1em;
  background-color: var(/*--light-cornflower-blue*/ --textColor);
  color: var(/*--orange*/ --headerColor);
  height: min-content;
}

.LinksList {
  margin: 0;
  list-style: none;
  padding: 0;
}

.listItem {
  color: var(/*--orange*/ --headerColor);
  padding: 0.2em;
  font-size: 1.25em;
}

.link {
  color: var(/*--prussian-blue*/ --backgroundColor);
}
